"use client"

import { IncidentDetails } from "@/types/incidentDetails"
import { IncidentDetailsTableLogic } from "./IncidentDetailsTableLogic/IncidentDetailsTableLogic"

interface Props {
  details: IncidentDetails[]
  setDetails: (updater: (prev: IncidentDetails[]) => IncidentDetails[]) => void
}

export default function IncidentDetailsTable({ details, setDetails }: Props) {
  return (
    <IncidentDetailsTableLogic details={details} setDetails={setDetails} />
  )
}
